import React from 'react';
var rotate = false;
class App extends React.Component {
	
		

   constructor(props) {
		
      super(props);
	  this.state = {
		  header: false
	  }
      this.forceUpdateHandler = this.forceUpdateHandler.bind(this);
	 
   };
   forceUpdateHandler() {
	
      this.forceUpdate();
   };
   

   render() {
      return (
			
         <div className="navBar">
		 {this.state.header ? "True" : "False"}
            <button className={this.state.header ? "button2" : "button1"} onClick = {this.forceUpdateHandler}>{this.state.header ? "Stop Rotation" : "Start Rotation"} </button>
				{this.state.header = !this.state.header}
         </div>
      );
   }
}

App.defaultProps = {
   headerProp: false,
   contentProp:true
}
export default App;


